@import "ScssHelpers/helpers-all.scss";

.product_gallery {
	position: relative;
	max-width: 100%;

	&:not(.article__slider &) {
		@include bp($point_2, min) {
			width: 56rem;
			margin-right: 6.4rem;
		}

		@include bp($point_3) {
			text-align: center;

			width: 100%;
			padding: 0 var(--gl-indent);

			&:not(:last-child) {
				margin-bottom: 2.6rem;
			}
		}
	}
}

.product_gallery__head {
	text-align: left;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}

		.article__slider & {
			display: none;
		}
	}
}

.product_gallery__title {
	font-size: 2.6rem;
	font-weight: 500;
	letter-spacing: -0.029em;
	line-height: 1.08;

	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}
}

.product_gallery__subtitle {
	color: $gray_2;
	font-size: 1.4rem;
	font-weight: 500;
	letter-spacing: 0.11em;
	line-height: 1.14;
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 0.4rem;
	}
}

.product_gallery__nav {
	display: flex;
	justify-content: space-between;
	position: absolute;
	z-index: 10;
	pointer-events: none;

	&:not(.article__slider &) {
		top: 50%;

		transform: translateY(-50%);

		@include bp($point_4, min) {
			left: -2.7rem;
			right: -2.7rem;

			// margin-top: -2.8rem;
		}
	}

	.article__slider & {
		@include bp($point_4, min) {
			bottom: 0;
			left: -2.4rem;
			right: -2.4rem;
		
			margin-bottom: 6.6rem;

			button {
				width: 4.8rem;
				height: 4.8rem;
			}
		}

		@include bp($point_4 - 1) {
			top: 50%;
			transform: translateY(-50%);
		}
	}

	@include bp($point_4 - 1) {
		left: -2rem;
		right: -2rem;
	}
}

.product_gallery__nav button {
	// padding: 1.8rem;
	// color: $black;
	z-index: 2;
	pointer-events: initial;
}

.product_gallery__main {
	// height: 100%;

	&:not(.article__slider &) {
		position: relative;

		@include bp($point_2, min) {
			width: 100%;
		}

		@include bp($point_3, $point_4) {
			width: 50%;
			margin-left: auto;
			margin-right: auto;
		}
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.product_gallery__main_picture,
.product_gallery__thumbs_picture {
	position: relative;
	width: 100%;
	height: 100%;
	display: block;
	overflow: hidden;
}

.product_gallery__thumbs_picture {
	&::before {
		z-index: 1;

		background: linear-gradient(29deg, $black 0%, rgba($black, 0) 100%);
		opacity: 0;
		content: "";

		transition: opacity 0.3s ease;
		pointer-events: none;

		@include stretch;
	}

	&:focus,
	&:hover {
		&::before {
			opacity: 0.5;
		}
	}
}

.product_gallery__main_img,
.product_gallery__thumbs_img {
	@include cover_img;
}

.product_gallery__thumbs .swiper-slide {
	cursor: pointer;

	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		z-index: 1;

		width: 100%;
		height: 100%;
		box-shadow: inset 0 0 0 0rem $gold_2;
		transition: box-shadow 0.2s linear;
		will-change: box-shadow;
		pointer-events: none;
	}

	&.swiper-slide-thumb-active {
		&:before {
			box-shadow: inset 0 0 0 0.5rem $gold_2;
		}
	}

	@include bp($point_4, min) {
		height: 18rem;
	}

	@include bp($point_4 - 1) {
		height: 100%;
	}
}

.product_gallery__video_link {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}

.product_gallery__icon {
	position: absolute;
	left: 50%;
	top: 50%;

	color: $white;
	z-index: 2;

	transform: translate(-50%, -50%);

	&:hover {
		color: $gold_2;

		transform: translate(-50%, -50%) scale(1.1);
	}

	.product_gallery__video_link & {
		@include bp($point_4, min) {
			width: 12rem;
			height: 12rem;
		}

		@include bp($point_4 - 1) {
			width: 7.2rem;
			height: 7.2rem;
		}
	}

	@include bp($point_4, min) {
		width: 7.2rem;
		height: 7.2rem;
		// margin-top: -3.6rem;
		// margin-left: -3.6rem;
	}

	@include bp($point_4 - 1) {
		width: 5rem;
		height: 5rem;
		// margin-top: -2.5rem;
		// margin-left: -2.5rem;
	}

	@include transition_all;
}
