@import "ScssHelpers/helpers-all.scss";

.single_product__breadcrumbs {
	@include bp($point_4, min) {
		padding: 2.6rem var(--gl-indent) 0;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		padding: 2rem var(--gl-indent) 0;
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.single_product__main {
	width: 100%;

	&:not(.section &) {
		max-width: $page_width;
		margin: 0 auto;

		@include bp($point_4, min) {
			&:not(:last-child) {
				margin-bottom: 8.8rem;
			}
		}

		@include bp($point_4 - 1) {
			&:not(:last-child) {
				margin-bottom: 4.8rem;
			}
		}
	}

	@include bp($point_2, min) {
		display: flex;
		flex-wrap: nowrap;
	}
}
